import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigIcms } from '../../../../models/dtos/mlf/configIcms';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const ConfigIcmsSearchView: FC<SearchViewProps<ConfigIcms>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'codigoReceita',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código da Receita',
    },
    {
      dataField: 'diaVencimento',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Dia de Vencimento',
    },
    {
      dataField: 'recolhimentoMinimo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Recolhimento Mínimo',
    },
  ];

  return (
    <WindowComponent title="Configurações de ICMS">
      <SearchDataGrid
        {...props}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ConfigPisEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ConfigIcmsSearchView;
